import React from 'react'
import styled from 'styled-components'
import Sections from './Sections'

function Home() {
  return (
    <div>
      <Container>
          <Sections 
            title = "Model S"
            description = "Order Online for Touchless Delivery"
            backgoundImg="model-s.jpg"
            leftBtnText="Custom order"
            rightBtnText="Existing inventory"
          />
          <Sections
             title = "Model Y"
            description = "Order Online for Touchless Delivery"
            backgoundImg="model-y.jpg"
            leftBtnText="Custom order"
            rightBtnText="Existing inventory"

          />
          <Sections
             title = "Model 3"
            description = "Order Online for Touchless Delivery"
            backgoundImg="model-3.jpg"
            leftBtnText="Custom order"
            rightBtnText="Existing inventory"
          />
          <Sections
             title = "Model X"
            description = "Order Online for Touchless Delivery"
            backgoundImg="model-x.jpg"
            leftBtnText="Custom order"
            rightBtnText="Existing inventory"
          />
          <Sections
             title = "Lowest Cost Solar Panels in America"
            description = "Money-back guarantee"
            backgoundImg="solar-panel.jpg"
            leftBtnText="Order Now"
            rightBtnText="Learn More"
          />
          <Sections
             title = "Solar for New Roofs"
            description = "Solar Roof Cost Less Than a New Roof Plus Solar Panels"
            backgoundImg="solar-roof.jpg"
            leftBtnText="Order Now"
            rightBtnText="Learn More"
          />
          <Sections
             title = "Accessories"
            // description = "Solar Roof Cost Less Than a New Roof Plus Solar Panels"
            backgoundImg="accessories.jpg"
            leftBtnText="Order Now"
       
          />
      </Container>
    </div>
    
  )
  }

export default Home

const Container = styled.div`
    height: 100vh;
    background-image:url('model-s.jpg')
    z-index:10;
`